body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  color: inherit;
}
.mainContainer {
  min-height: 1000px;
  height: 100vh;
  overflow: hidden;
}
.navigationContainer {
  height: 5%;
  background-color: rgb(40, 163, 216);
}
.headerOneContainer {
  display: flex;
  height: 35%;
  width: 90%;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .mainContainer {
    height: 200vh;
    .headerOneContainer {
      display: flex;
      flex-direction: column;
      height: 20%;
      width: 90%;
    }
    .headerTwoContainer {
      height: 35%;
    }
    .headerThreeContainer {
      flex-direction: column;
      align-items: center;
    }
    .footer {
      height: 5%;
    }
  }
}
.headerTwoContainer {
  width: 100vw;
  height: 20%;
}
.headerThreeContainer {
  display: flex;
  height: 35%;
  width: 75vw;
  margin: 0 auto;
}
.footer {
  background-color: black;
  height: 5%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
